}

.nav__link {
  white-space: nowrap;
  text-transform: capitalize;
  color: var(--color-white);
  font-variation-settings: unset;
  background-color: #0000;
  border: 1px solid #0000;
  padding: 2px;
  font-size: 20px;
  line-height: 1.2;
