}

.nav__btns .request-btn {
  white-space: nowrap;
  color: #fff;
  background-color: var(--color-blue-650);
  max-width: 145px;
}

@media only screen and (width <= 992px) {
  .nav__btns {
    flex-direction: column;
  }

  .nav .nav__list {
    margin: 0 auto;
  }

  .nav .nav__item {
    margin-right: 0;
  }

  .nav--footer {
    flex-direction: column;
  }
}

@media only screen and (width <= 992px) and (width <= 576px) {
  .nav--footer .nav__list {
    flex-direction: column;
  }
}

@media only screen and (width <= 992px) {
  .nav--footer .nav__inner {
    flex-direction: column;
    align-items: center;
  }
}

[class].btn {
  font-family: var(--font-family-primary);
  font-weight: var(--fw-500);
  white-space: nowrap;
  text-align: center;
  color: inherit;
  background-color: inherit;
  border: 1px solid #0000;
  border-radius: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 15px 32px;
  font-size: 20px;
  line-height: 1;
  transition-property: background-color, color, border-color;
  transition-duration: .2s;
  display: flex;
  position: relative;
}

[class].btn:hover, [class].btn:focus {
  color: var(--color-blue-100);
}

[class].btn--reverse:hover, [class].btn--reverse:focus {
  background-color: #0000;
  border-image: url("../images/button-bg.png") 1 round;
  color: var(--color-blue-100) !important;
}

[class].btn--user {
  position: relative;
}

[class].btn--user:hover:before, [class].btn--user:focus:before {
  filter: invert();
}

@media only screen and (width <= 475px) {
  [class].btn {
    min-width: auto;
    margin: 0 auto;
  }
}

[class].request-btn {
  position: relative;
}

[class].request-btn:hover:after, [class].request-btn:focus:after {
  filter: invert(0);
}

.arrow {
  color: var(--color-blue-150);
  padding-right: 32px;
  transition: all .4s;
  display: inline-block;
  position: relative;
}

.arrow:after {
  content: "";
  background-image: url("../images/icons/arrow-next.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 20px;
  height: 20px;
  transition: all .4s;
  position: absolute;
  top: 50%;
  right: -12px;
  transform: translateY(-50%);
}

.burger {
  --line-height: 2px;
  --buger-size: 25px;
  --move: calc(var(--buger-size) / 3);
  --move-decrement: calc(var(--move) * -1);
  --transition-time: .5s;
  --lines-color: var(--color-white);
  width: calc(var(--buger-size)  + 5px);
  height: var(--buger-size);
  transition: opacity var(--transition-time) ease;
  background-color: #0000;
  display: none;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.burger span {
  z-index: -1;
  height: var(--line-height);
  background-color: var(--lines-color);
  transition: transform var(--transition-time) ease, background-color var(--transition-time) ease .3s;
  width: 100%;
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.burger span:before, .burger span:after {
  content: "";
  height: var(--line-height);
  background-color: var(--lines-color);
  width: 100%;
  animation-duration: .4s;
  animation-timing-function: cubic-bezier(.645, .045, .355, 1);
  animation-fill-mode: forwards;
  position: absolute;
  left: 0;
}

.burger span:before {
  transform: translateY(var(--move-decrement)) rotate(0);
}

.burger span:after {
  transform: translateY(var(--move)) rotate(0);
}

.burger:hover {
  opacity: 1;
}

@media only screen and (width <= 992px) {
  .burger {
    display: block;
  }
}

.closed.opened span {
  background-color: var(--lines-color);
  transition: background-color .2s;
}

.closed.opened span:before {
  animation-name: toplineburger;
}

.closed.opened span:after {
  animation-name: bottomlineburger;
}

.opened .nav__inner {
  gap: 40px;
  transform: translateX(0);
}

.opened span {
  background-color: #0000;
  transition: background-color .2s;
}

.opened span:before, .opened span:after {
  animation-duration: .2s;
  animation-timing-function: cubic-bezier(.645, .045, .355, 1);
  animation-fill-mode: forwards;
}

.opened span:before {
  animation-name: toplinecross;
}

.opened span:after {
  animation-name: bottomlinecross;
}

@keyframes toplinecross {
  0% {
    transform: translateY(var(--move-decrement));
  }

  50% {
    transform: translateY(0);
  }

  100% {
    transform: rotate(45deg);
  }
}

@keyframes bottomlinecross {
  0% {
    transform: translateY(var(--move));
  }

  50% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(0)rotate(-45deg);
  }
}

@keyframes toplineburger {
  0% {
    transform: rotate(45deg);
  }

  50% {
    transform: rotate(0);
  }

  100% {
    transform: translateY(var(--move-decrement)) rotate(0);
  }
}

@keyframes bottomlineburger {
  0% {
    transform: translateY(0)rotate(-45deg);
  }

  50% {
    transform: rotate(0);
  }

  100% {
    transform: translateY(var(--move)) rotate(0);
  }
}

